import api from "./api";

// Login function - Uses Django JWT /api/token/
const login = async (email, password) => {
  try {
    // Django JWT expects 'username' field
    // If your users log in with email, you might need to pass email as username
    const response = await api.post("/token/", { 
      username: email,  // or just 'email: email' if your backend accepts email
      password 
    });
    
    // Django JWT returns { access, refresh }
    return {
      token: response.data.access,
      refresh: response.data.refresh
    };
  } catch (error) {
    throw error.response?.data || { message: "Login failed" };
  }
};

// Signup function - FIXED to match Django UserSerializer
const signup = async (signupData) => {
  try {
    // Django expects: username, email, password, password_confirm, phone_number (optional)
    const payload = {
      username: signupData.username,
      email: signupData.email,
      password: signupData.password,
      password_confirm: signupData.password_confirm,
    };

    // Only add phone_number if it exists
    if (signupData.phone_number) {
      payload.phone_number = signupData.phone_number;
    }

    console.log("Signup payload:", payload); // Debug log

    const response = await api.post("/register/", payload);
    console.log("Signup response:", response.data); // Debug log
    
    return response.data;
  } catch (error) {
    console.error("Signup error:", error.response?.data); // Debug log
    throw error.response?.data || { message: "Signup failed" };
  }
};

// Get user profile (JWT automatically attached via interceptor)
const getProfile = async () => {
  try {
    const response = await api.get("/profile/");
    // Backend returns UserProfileSerializer
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch profile" };
  }
};

// Get user dashboard data
const getDashboard = async () => {
  try {
    const response = await api.get("/dashboard/");
    return response.data;
  } catch (error) {
    throw error.response?.data || { message: "Failed to fetch dashboard" };
  }
};

// Refresh token
const refreshToken = async () => {
  try {
    const refresh = localStorage.getItem(
      process.env.NEXT_PUBLIC_JWT_REFRESH_KEY || "refresh_token"
    );
    
    if (!refresh) {
      throw new Error("No refresh token available");
    }

    const response = await api.post("/token/refresh/", { refresh });
    const newAccessToken = response.data.access;
    
    // Update access token in storage
    localStorage.setItem(
      process.env.NEXT_PUBLIC_JWT_STORAGE_KEY,
      newAccessToken
    );
    
    return newAccessToken;
  } catch (error) {
    // If refresh fails, clear tokens and logout
    logout();
    throw error.response?.data || { message: "Session expired" };
  }
};

// Logout
const logout = () => {
  localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY);
  localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_REFRESH_KEY || "refresh_token");
};

export default {
  login,
  signup,
  getProfile,
  getDashboard,
  refreshToken,
  logout,
};